function dianji(fn) {
	//
	let {
		onoff
	} = getApp().globalData;

	if (onoff) {
		getApp().globalData.onoff = false;
		fn();
		setTimeout(function() {
			getApp().globalData.onoff = true;
		}, 1500)
	} else {
		//elsedataonoff:true,else
		console.log("")
	}
}
export {
	dianji
}
